.app_main_area {
  display: flex;
  height: 100vh;
  width: 100%;
}

.menu_bar {
  width: 250px;
  height: 100%;
}

.mobile_menu_bar{
   position: absolute;
   z-index: 100;
}

.main_screen {
  flex: 1;
  overflow: auto;
}

@media (max-width: 650px) {
  .menu_bar {
    display: none; /* Hide the menu_bar on screens less than 600px wide */
  }

  .main_screen {
    width: 100%; /* Occupy the full width on smaller screens */
  }
}

.no_data_text{
  position: fixed;
  left: 40%;
  top: 50%;
  font-size: 40px;
  width: 40%;
}

.approvel_button{
  font-size: 15px;
  font-weight: 650;
  border: 0px;
  background-color: #f1cf59;
  color: black;
  border-radius: 6px;
}