.container{
    width: 240px;
    display: flex;
    position: fixed;
    left: 0px;
    margin-left: -13px;
    min-height: 100vh; 
  }

.sidebars{
    background:#272A2E;
    color: #fff;
    height: 100vh;
    width: inherit;
    overflow: auto;
}
.topsection{
    display: flex;
    align-items: flex-end;
    padding: 20px 30px;
    margin-bottom: 30px;
}
.logo{
    font-size: 30px;
}
.bars{
    position: absolute;
    font-size: 35px;
    margin-left: 10px;
    z-index: 101;
    margin-top: -5px;
}
.open_bar_class{
    position: absolute;
    font-size: 35px;
    margin-left: 167px;
    z-index: 101;
    margin-top: -5px;
    background-color: #272A2E;
}
.link{
    display: flex;
    color: #fff;
    padding: 10px 15px;
    gap: 15px;
    transition: all 0.5;
    margin-bottom: 40px;
    text-decoration: none;
}
.link:hover{
    background:#f1cf59;
    color: #fff;
    transition: all 0.5s;
}
.active{
    background: #f1cf59;
    color: #000;
}
.icon {
    font-size: 20px;
}
.link_text{
    font-size: 20px;
    text-decoration: none; 
}