.orders_main_area{
    width: inherit;
    margin-top: 20px;
}
/* header */

.all_orders_header{
    margin-top: 19px;
    display: grid;
    grid-template-columns: repeat(auto-fit,minmax(200px,1fr));
    gap: 10px;
}

/*.header_left{
}*/

.search_bar{
    margin-top: 5px;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 0px solid white;
    border-radius: 8px;
    padding-left: 5px;
    width: 90%;
    background-color: #272A2E;
    height: 40px;
    box-shadow: 0px 0px 10px rgb(13, 13, 13);
    margin-bottom: 20px;
}

.search_bar input{
    background-color: rgba(40, 40, 40, 0);
    border: 0px;
    /* text-align: center; */
    margin-left:6px;
    color: white;
    width: 90%;
    margin-right: 5px;
}

.Search_Icon{
    font-size: 25px;
    margin-left: 10px;
}
.order_details_body .icons{
    color: #f1cf59;
}
.order_details_body .iconsLocation{
    color: gray;
}
.header_right {
    display: flex;
    justify-content: space-around;
    align-items: center;
    text-align: center;
}

.header_right select{
    color: white;
    margin: 5px;
    padding: 2px;
    border-radius: 8px;
    height: 40px;
    border: 0px;
    box-shadow: 0px 0px 10px rgb(13, 13, 13);
    background-color: #272A2E;

}

.header_right select option{
    background-color: rgb(0, 0, 0);
    color: white;
}
/* header end */


/* order detail grid */
.main_area{
    display: grid;
    grid-template-columns: repeat(auto-fit,minmax(250px,1fr));
    gap: 20px;
    padding: 15px;
}

.order_details{
    border: 3px solid white;
    padding: 10px;
    border-radius: 8px;
    border: 0px;
    box-shadow: 0px 0px 10px rgb(13, 13, 13);
    background-color: #272A2E;
}

.order_details_header{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.order_details_header h3 {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: 150px; /* Adjust the max width as needed */
}

.order_details_body{
    max-width: 100%;
}

.order_details_footer{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

/* order detail grid end*/