.vendorDetails{
  width: inherit;
  margin-top: 20px;
  margin-bottom: 20px;
}
/* header */

.all_vendor_header{
  margin-top: 19px;
  display: grid;
  grid-template-columns: repeat(auto-fit,minmax(400px,1fr));
  gap: 10px;
}
.search_bar{
  margin-top: 30px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 0px solid white;
  border-radius: 8px;
  padding-left: 5px;
  width: 70%;
  background-color: #272A2E;
  height: 50px;
  box-shadow: 0px 0px 10px rgb(13, 13, 13);
}

.search_bar input{
  background-color: rgba(40, 40, 40, 0);
  border: 0px;
  text-align: center;
  margin-left: 2px;
  color: white;
  width: 99%;
  margin-right: 5px;
}

.Search_Icon{
  font-size: 24px;
  margin-left: 15px;
}

.header_right {
  display: flex;
  justify-content: space-around;
  align-items: center;
  text-align: center;
}

.header_right select{
  color: white;
  margin: 5px;
  padding: 2px;
  border-radius: 8px;
  height: 40px;
  border: 0px;
  box-shadow: 0px 0px 10px rgb(13, 13, 13);
  background-color: #272A2E;

}

.header_right select option{
  background-color: rgb(0, 0, 0);
  color: white;
}
/* header end */


/* order detail grid */
.main_area{
  display: grid;
  grid-template-columns: repeat(auto-fit,minmax(100px,1fr));
  gap: 30px;
  padding: 15px;
  margin-bottom: 20%;
  margin-top: 20px;
}

.vendor_details{
  border: 3px solid white;
  padding: 10px;
  border-radius: 8px;
  border: 0px;
  box-shadow: 0px 0px 10px rgb(13, 13, 13);
  background-color: #272A2E;
}

.vendor_details_header{
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.vendor_details_body{
  max-width: 100%;
  text-align: center;
}

.vendor_details_footer{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

