.order_detail_main_area {
  padding: 0px 60px;
  background-color: #272a2eb7;
  position: fixed;
  top: 0;
  width: 85%;
  height: 100%;
  overflow: auto;
}

.order_detail_close {
  font-size: 35px;
  display: flex;
  margin-top: 10px;
  justify-content: end;
  color: #f1cf59;
  max-width: 1300px;
  width: 99%;
}
.order_id_top{
  color: #f1cf59;
}
.grid_container{
  display: grid;
  grid-template-columns: repeat(auto-fit,minmax(300px,1fr));
  max-width: 1400px;
  width: 99%;
  gap: 10px;
}

.section_title h2 {
  text-align: center;
  margin-right: 15px;
  display: flex;
  align-items: start;
}

.section_title {
  padding-bottom: 30px;
}

.section_title h2 {
  font-size: 32px;
  font-weight: bold;
  margin-bottom: 15px;
  padding-bottom: 15px;
  position: relative;
  color: #345676;
}

.section_title h2::after {
  content: "";
  position: absolute;
  display: block;
  width: 50px;
  height: 3px;
  background: #f1cf59;
  bottom: 0;
  left: 0;
}

.section_title p {
  margin-bottom: 0;
}

@media (max-width : 650px) {
  .order_detail_main_area {
    left: 0px;
    width: 100%;
  }
  .order_detail_close{
    justify-content: end;
  }
}

@media (max-width : 900px) {
  .order_detail_close{
    justify-content: center;
  }
  .grid_container{
    width: 99%;
  }
  .grid-item {
    width: 70%;
  }
}
@media (max-width : 1100px) {
  .order_detail_close{
    justify-content: center;
    margin-right: 1%;
  }
}

