.vendor_detail_main_area {
    padding: 0px 60px;
  }
  
 
.vendor {
    overflow: auto; /* Allow horizontal scrolling */
}

.vendor .row {
    margin-top: 30px;
    padding-right: 15px; /* Adjust the padding for better mobile view */
    display: flex;
    justify-content: center;

}

.inner_area{
  display: flex;
  width: 90%;
}
/* Center the section title in mobile view */
.section-title h2 {
    text-align: center;
}
  .section-title {
  
    padding-bottom: 30px;
  }
  
  .section-title h2 {
   
    font-size: 32px;
    font-weight: bold;
    margin-bottom: 15px;
    padding-bottom: 15px;
    position: relative;
    color: #345676;
  }
  
  .section-title h2::after {
    content: "";
    position: absolute;
    display: block;
    width: 50px;
    height: 3px;
    background: #f1cf59;
    bottom: 0;
    left: 0;
  }
  
  .section-title p {
    margin-bottom: 0;
  }


.vendor .vendor-title {
    font-size: 26px;
    font-weight: 500;
    margin-top: 20px;
    margin-bottom: 20px;
    color: #4b7dab;
    font-family: "Poppins", sans-serif;
  }
  
  .vendor .vendor-item {
    padding: 0 0 20px 20px;
    margin-top: -2px;
    border-left: 2px solid #f1cf59;
    position: relative;
  }
  
  .vendor .vendor-item h4 {
    line-height: 18px;
    font-size: 18px;
    font-weight: 600;
    color: #B1B1B1;
    margin-bottom: 10px;
  }
  
  .vendor .vendor-item h5 {
    font-size: 16px;
    color: #fff;
    padding: 5px 15px;
    display: inline-block;
    font-weight: 900;
    margin-bottom: 10px;
    font-family: "Poppins", sans-serif;
  }
  
  .vendor .vendor-item ul {
    padding-left: 20px;
  }
  
  .vendor .vendor-item ul li {
    padding-bottom: 10px;
  }
  
  .vendor .vendor-item:last-child {
    padding-bottom: 0;
  }
  
  .vendor .vendor-item::before {
    content: "";
    position: absolute;
    width: 16px;
    height: 16px;
    border-radius: 50px;
    left: -9px;
    top: 0;
    background: #fff;
    border: 2px solid #f1cf59;
  }
  .vendor .row{
    margin-top: 30px;
    padding-right: 60px;
  }
  /* column  */
  .vendor .col-lg-6{
    margin-right: 10px;
  }
  