.rating_detail_main{
    border-radius: 10px;
    padding: 10px;
    background-color: #272A2E;
    justify-content: center;
    position: fixed;
    top: 10%;
    left: 35%;
    width: 40%;
    height: 50%;
    overflow: auto;
}

@media (max-width : 700px){
    .rating_detail_main{
        border-radius: 10px 0px 0px 10px ;
        position: fixed;
        top: 5%;
        left: 5%;
        width: 90%;
        height: 90%;
    }
}

.rating_detail_header{
    display: flex;
    justify-content: end;
    font-size: 30px;
    color: #f1cf59;
    
}

.blur_item{
    filter: blur(5px);
}